                 open: false
               },
             ]
        };
    },
  methods: {
    updateScroll() {
       this.scrollPosition = window.scrollY
    },
    toggleOpen: function (index) {
      this.faqs = this.faqs.map((faq, i) => {
        if(index === i) {
          faq.open = !faq.open;
        } else {
          faq.open = false;
        }
        return faq;
      })
    }
},

