<template>    


    <transition name="fade">
        <div v-if="show" class="preloader">
            <img src="@/assets/img/Logo.png" width="100px" onselectstart="return false" oncontextmenu="return false" ondragstart="return false" onMouseOver="window.status='..message perso .. '; return true;" >
        </div>
    </transition>




</template>

<script>

export default {


  name: 'Preloader',
  data(){
      return {
          show: true   
      }
  },

  mounted() {
      this.showToggle()
  },

  methods: {
      showToggle() {
          setTimeout(() => {
              this.show = false
          }, 1000)
      }
  }

}
</script>

<style lang="scss" scoped>
    @import './preloader.scss';

 
</style> 